@font-face {
  font-family: "Firasans";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/firasans-bold-webfont.woff2") format("woff2");
  src: url("../fonts/firasans-bold-webfont.woff") format("woff"); }

@font-face {
  font-family: "Firasans";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: url("../fonts/firasans-medium-webfont.woff2") format("woff2");
  src: url("../fonts/firasans-medium-webfont.woff") format("woff"); }

@font-face {
  font-family: "Firasans";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/firasans-regular-webfont.woff2") format("woff2");
  src: url("../fonts/firasans-regular-webfont.woff") format("woff"); }

@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/montserrat-bold-webfont.woff2") format("woff2");
  src: url("../fonts/montserrat-bold-webfont.woff") format("woff"); }

@font-face {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 800;
  font-display: swap;
  src: url("../fonts/raleway-extrabold-webfont.woff2") format("woff2");
  src: url("../fonts/raleway-extrabold-webfont.woff") format("woff"); }

.visually-hidden,
input[type="file"].visually-hidden,
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%); }

.site-wrapper {
  flex: 1 1 auto;
  display: flex;
  flex-flow: column nowrap;
  min-height: 100vh;
  overflow-x: hidden; }
  .site-wrapper .main-content {
    flex: 1 0 auto; }

.scroll-lock {
  position: fixed;
  max-height: 100vh;
  left: 0;
  right: 0; }

.btn-reset {
  cursor: pointer;
  padding: 0;
  background-color: transparent;
  border: none; }

body {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  background-color: #ffffff; }

html,
body {
  margin: 0;
  padding: 0; }

*,
*::before,
*::after {
  box-sizing: inherit; }

html {
  box-sizing: border-box; }

img,
video {
  max-width: 100%;
  height: auto; }

textarea {
  resize: vertical; }

/* :focus {
  outline: none;
} */
input::placeholder,
textarea::placeholder {
  opacity: 1; }

input::-ms-clear, input::-ms-reveal {
  display: none; }

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  appearance: none; }

input:-webkit-autofill {
  animation-name: autofill;
  animation-delay: 1s;
  animation-fill-mode: both; }

@keyframes autofill {
  0%,
  100% {
    color: #000000;
    background: transparent; } }

textarea,
input:matches([type="email"],
[type="number"],
[type="password"],
[type="search"],
[type="tel"],
[type="text"],
[type="url"]) {
  appearance: none; }

body {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.2;
  font-family: "Arial", sans-serif;
  color: #000000;
  background-color: #ffffff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

a {
  text-decoration: none; }

.container {
  margin: 0 auto; }

.main-header {
  background-color: skyblue;
  padding-left: 50px; }

.main-content {
  padding: 50px;
  width: 100%;
  background-color: pink; }

.main-footer {
  background-color: skyblue; }
